.notification-item {
    background: #fff;
    padding: 20px 40px;
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
}

.notification-icon-text-wrapper {
    display: flex;
    flex-direction: row;
    gap: 30px;
    align-items: center;
    color: #009595;
    font-size: 24px;
}

.notification-time {
    color: #555555;
}
