.dashboard-home-card-items {
    position: relative;
    width: 48.5%;
    height: 200px;
    border-radius: 20px;
    padding: 0 50px;
    color: #fff;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

.dashboard-home-card-text {
    align-self: center;
    width: 80%;
    z-index: 1;
}

.dashboard-home-card-text .dashboard-home-card-text-title {
    font-size: 30px;
    font-weight: 600;
}

.dashboard-home-card-text h1 {
    font-size: 52px;
    margin-bottom: 15px;
}

.dashboard-home-card-text .dashboard-home-card-text-detail {
    font-size: 18px;
    color: rgba(255, 255, 255, 0.7);
    display: flex;
    align-items: center;
    font-weight: 600;
    transition: ease-in-out 0.1s;
    width: fit-content;
}

.dashboard-home-card-icon-detail {
    height: 18px !important;
    width: 25px !important;
    background: rgba(255, 255, 255, 0.7);
    margin-left: 5px;
}

.dashboard-home-card-text-detail:hover {
    color: rgba(255, 255, 255, 1);
}

.dashboard-home-card-text-detail:hover .dashboard-home-card-icon-detail {
    background: rgba(255, 255, 255, 1);
}

.dashboard-home-card-icon {
    /* width: 50%; */
    align-self: center;
    text-align: end;
    z-index: 1;
}

.dashboard-home-card-icon i {
    background: #fff;
    width: 113px;
    height: 113px;
}

.dashboard-home-card-ornament {
    background: url(/public/assets/icons/card-ornament01.svg);
    height: 100px;
    width: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    right: 0;
    bottom: 0;
}